import * as React from "react"
import styled from "styled-components"

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  min-height: 100vh;
  position: relative;
`


// markup
const IndexPage = () => {

  const test = () =>{
    if(typeof window !== undefined){
        return window.innerHeight
    }return null 
  }

  return (
    <Wrapper>
    {
      <span>{test()}</span>
    }
    </Wrapper>
  )
}

export default IndexPage
